// Trang dành cho nhân viên
'use client';

import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { getDocs, query, where, collection } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { useUser } from '@/src/context/AuthContext';
import RentalCompanyDashboard from '@/src/components/my-business/dashboards/RentalCompanyDashboard';
import PrivateProviderDashboard from '@/src/components/my-business/dashboards/PrivateProviderDashboard';
import AgentDashboard from '@/src/components/my-business/dashboards/AgentDashboard';
import { Button } from '@/src/components/ui/button';

interface Business {
  id: string;
  name: string;
  type: 'rental_company' | 'private_provider' | 'agent';
}

export default function MyBusinessPage() {
  const { user } = useUser();
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [businesses, setBusinesses] = useState<Business[]>([]);

  useEffect(() => {
    const fetchBusinesses = async () => {
      if (!user?.uid) return;

      const rentalQuery = query(collection(db, 'rentalCompanies'), where('ownerId', '==', user.uid));
      const agentQuery = query(collection(db, 'agents'), where('ownerId', '==', user.uid));

      const [rentalSnap, agentSnap] = await Promise.all([
        getDocs(rentalQuery),
        getDocs(agentQuery),
      ]);

      const loaded: Business[] = [];

      rentalSnap.forEach((doc) => {
        loaded.push({
          id: doc.id,
          name: doc.data().name || 'Rental Company',
          type: 'rental_company',
        });
      });

      agentSnap.forEach((doc) => {
        loaded.push({
          id: doc.id,
          name: doc.data().name || 'Agent',
          type: 'agent',
        });
      });

      if (loaded.length === 0) {
        router.replace('/my-business/create');
      } else {
        setBusinesses(loaded);
      }

      setLoading(false);
    };

    fetchBusinesses();
  }, [user, router]);

  const handleSelectBusiness = (business: Business) => {
    if (business.type === 'rental_company') {
      router.push(`/my-business/rental-company/${business.id}`);
    } else if (business.type === 'private_provider') {
      router.push(`/my-business/private-provider/${business.id}`);
    } else if (business.type === 'agent') {
      router.push(`/my-business/agent/${business.id}`);
    }
  };

  if (loading) return <div className="text-center py-10">Loading...</div>;

  if (businesses.length === 1) {
    // Nếu chỉ có 1 business -> tự động điều hướng luôn
    handleSelectBusiness(businesses[0]);
    return null;
  }

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-6 text-center">Select Your Business</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {businesses.map((business) => (
          <Button
            key={business.id}
            onClick={() => handleSelectBusiness(business)}
            className="w-full py-6 text-lg font-semibold"
          >
            {business.name}
          </Button>
        ))}
      </div>
    </div>
  );
}
